.chat-container {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
  height: 600px;
  display: flex;
  flex-direction: column;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  overflow: hidden;
}
.chat-container .messages-area {
  flex: 1;
  overflow-y: auto;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.chat-container .messages-area .empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
  color: #6b7280;
}
.chat-container .messages-area .empty-state .empty-icon {
  margin-bottom: 1rem;
  color: #d1d5db;
}
.chat-container .messages-area .empty-state .empty-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: #374151;
  margin: 0 0 0.5rem 0;
}
.chat-container .messages-area .empty-state .empty-description {
  font-size: 0.875rem;
  color: #6b7280;
  margin: 0;
  max-width: 24rem;
  line-height: 1.5;
}
.chat-container .messages-area .message {
  display: flex;
  gap: 0.75rem;
  max-width: 80%;
}
.chat-container .messages-area .message.message-user {
  align-self: flex-end;
  flex-direction: row-reverse;
}
.chat-container .messages-area .message.message-user .message-content {
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  color: white;
}
.chat-container .messages-area .message.message-user .message-content .message-text {
  color: white;
}
.chat-container .messages-area .message.message-user .message-content .message-timestamp {
  color: rgba(255, 255, 255, 0.7);
}
.chat-container .messages-area .message.message-assistant {
  align-self: flex-start;
}
.chat-container .messages-area .message.message-assistant .message-content {
  background: #f8fafc;
  border: 1px solid #e2e8f0;
}
.chat-container .messages-area .message .message-avatar {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  background: #f3f4f6;
  color: #6b7280;
  flex-shrink: 0;
  margin-top: 0.25rem;
}
.chat-container .messages-area .message .message-content {
  padding: 0.75rem 1rem;
  border-radius: 1rem;
  position: relative;
  min-width: 0;
}
.chat-container .messages-area .message .message-content .message-text {
  font-size: 0.875rem;
  line-height: 1.5;
  color: #374151;
  word-wrap: break-word;
}
.chat-container .messages-area .message .message-content .message-text :global(h1), .chat-container .messages-area .message .message-content .message-text :global(h2), .chat-container .messages-area .message .message-content .message-text :global(h3) {
  margin: 0 0 0.5rem 0;
  font-weight: 600;
}
.chat-container .messages-area .message .message-content .message-text :global(ul), .chat-container .messages-area .message .message-content .message-text :global(ol) {
  margin: 0.5rem 0;
  padding-left: 1.5rem;
}
.chat-container .messages-area .message .message-content .message-text :global(li) {
  margin: 0.25rem 0;
}
.chat-container .messages-area .message .message-content .message-text :global(code) {
  background: rgba(0, 0, 0, 0.1);
  padding: 0.125rem 0.25rem;
  border-radius: 0.25rem;
  font-family: "Courier New", monospace;
  font-size: 0.8em;
}
.chat-container .messages-area .message .message-content .message-text :global(strong) {
  font-weight: 600;
}
.chat-container .messages-area .message .message-content .message-text :global(em) {
  font-style: italic;
}
.chat-container .messages-area .message .message-content .message-actions {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  opacity: 0;
  transition: opacity 0.2s ease;
}
.chat-container .messages-area .message .message-content .message-actions .action-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 1.75rem;
  height: 1.75rem;
  border: none;
  background: rgba(0, 0, 0, 0.1);
  border-radius: 0.375rem;
  color: #6b7280;
  cursor: pointer;
  transition: all 0.2s ease;
}
.chat-container .messages-area .message .message-content .message-actions .action-button:hover {
  background: rgba(0, 0, 0, 0.2);
  color: #374151;
}
.chat-container .messages-area .message .message-content .message-timestamp {
  font-size: 0.75rem;
  color: #9ca3af;
  margin-top: 0.25rem;
}
.chat-container .messages-area .message .message-content:hover .message-actions {
  opacity: 1;
}
.chat-container .messages-area .message .processing-indicator {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: #6b7280;
}
.chat-container .messages-area .message .processing-indicator .processing-dots {
  display: flex;
  gap: 0.25rem;
}
.chat-container .messages-area .message .processing-indicator .processing-dots span {
  width: 0.375rem;
  height: 0.375rem;
  background: #9ca3af;
  border-radius: 50%;
  animation: processing 1.4s infinite ease-in-out both;
}
.chat-container .messages-area .message .processing-indicator .processing-dots span:nth-child(1) {
  animation-delay: -0.32s;
}
.chat-container .messages-area .message .processing-indicator .processing-dots span:nth-child(2) {
  animation-delay: -0.16s;
}
.chat-container .messages-area .message .processing-indicator .processing-dots span:nth-child(3) {
  animation-delay: 0s;
}
.chat-container .messages-area .message .processing-indicator .processing-text {
  font-size: 0.875rem;
  font-style: italic;
}
.chat-container .input-area {
  border-top: 1px solid #e5e7eb;
  padding: 1rem 1.5rem;
  background: #f8fafc;
}
.chat-container .input-area .input-container {
  display: flex;
  align-items: flex-end;
  gap: 0.75rem;
  background: white;
  border: 1px solid #d1d5db;
  border-radius: 0.75rem;
  padding: 0.75rem;
  transition: all 0.2s ease;
}
.chat-container .input-area .input-container:focus-within {
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}
.chat-container .input-area .input-container .message-input {
  flex: 1;
  border: none;
  outline: none;
  resize: none;
  font-size: 0.875rem;
  line-height: 1.5;
  color: #374151;
  background: transparent;
  min-height: 1.25rem;
  max-height: 120px;
}
.chat-container .input-area .input-container .message-input::placeholder {
  color: #9ca3af;
}
.chat-container .input-area .input-container .message-input:disabled {
  color: #9ca3af;
  cursor: not-allowed;
}
.chat-container .input-area .input-container .send-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.5rem;
  height: 2.5rem;
  border: none;
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  color: white;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: all 0.2s ease;
  flex-shrink: 0;
}
.chat-container .input-area .input-container .send-button:hover:not(:disabled) {
  background: linear-gradient(135deg, #2563eb 0%, #1e40af 100%);
  transform: translateY(-1px);
}
.chat-container .input-area .input-container .send-button:active:not(:disabled) {
  transform: translateY(0);
}
.chat-container .input-area .input-container .send-button:disabled {
  background: #d1d5db;
  color: #9ca3af;
  cursor: not-allowed;
  transform: none;
}
.chat-container .input-area .input-hint {
  font-size: 0.75rem;
  color: #6b7280;
  margin-top: 0.5rem;
  text-align: center;
}

@keyframes processing {
  0%, 80%, 100% {
    transform: scale(0);
  }
  40% {
    transform: scale(1);
  }
}
@media (max-width: 768px) {
  .chat-container {
    height: 500px;
  }
  .chat-container .messages-area {
    padding: 1rem;
  }
  .chat-container .messages-area .message {
    max-width: 90%;
  }
  .chat-container .messages-area .message .message-content .message-actions {
    opacity: 1;
  }
  .chat-container .input-area {
    padding: 0.75rem 1rem;
  }
  .chat-container .input-area .input-container .message-input {
    font-size: 1rem;
  }
}
